import React, { useEffect, useRef, useState } from "react";
import * as d3 from "d3";
import { fetchWeatherApi } from "openmeteo";

const WeatherGraph = () => {
  const svgRef = useRef();
  const [weatherData, setWeatherData] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      const params = {
        latitude: 59.3294,
        longitude: 18.0687,
        hourly: ["temperature_2m", "rain", "weather_code"],
        daily: [
          "weather_code",
          "temperature_2m_max",
          "temperature_2m_min",
          "apparent_temperature_max",
          "apparent_temperature_min",
          "sunrise",
          "sunset",
          "daylight_duration",
          "sunshine_duration",
          "uv_index_max",
          "uv_index_clear_sky_max",
          "precipitation_sum",
          "rain_sum",
          "showers_sum",
          "snowfall_sum",
          "wind_speed_10m_max",
          "wind_gusts_10m_max",
          "wind_direction_10m_dominant",
        ],
        timezone: "Europe/Berlin",
      };
      const url = "https://api.open-meteo.com/v1/forecast";
      const responses = await fetchWeatherApi(url, params);
      const response = responses[0];

      const hourly = response.hourly();
      const time = range(
        Number(hourly.time()),
        Number(hourly.timeEnd()),
        hourly.interval()
      ).map((t) => new Date((t + response.utcOffsetSeconds()) * 1000));
      const temperature = hourly.variables(0).valuesArray();
      const rain = hourly.variables(1).valuesArray();
      const sunshine = hourly.variables(2).valuesArray();

      setWeatherData({ time, temperature, rain, sunshine });
    };

    fetchData();
  }, []);

  useEffect(() => {
    if (weatherData) {
      const { time, temperature, rain, sunshine } = weatherData;

      const margin = { top: 20, right: 30, bottom: 30, left: 60 };
      const width = 800 - margin.left - margin.right;
      const height = 400 - margin.top - margin.bottom;

      const svg = d3
        .select(svgRef.current)
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform", `translate(${margin.left},${margin.top})`);

      // Filter time array to include only current day
      const today = new Date();
      const startTime = new Date(
        today.getFullYear(),
        today.getMonth(),
        today.getDate(),
        0,
        0,
        0
      );
      const endTime = new Date(
        today.getFullYear(),
        today.getMonth(),
        today.getDate() + 1,
        0,
        0,
        0
      );
      const filteredTime = time.filter((t) => t >= startTime && t < endTime);

      const x = d3
        .scaleTime()
        .domain(d3.extent(filteredTime))
        .range([0, width]);

      const yTemperature = d3
        .scaleLinear()
        .domain([d3.min(temperature), d3.max(temperature)])
        .range([height, 0]);

      const yRain = d3
        .scaleLinear()
        .domain([0, d3.max(rain)])
        .range([height, 0]);

      const ySunshine = d3
        .scaleLinear()
        .domain([0, d3.max(sunshine)])
        .range([height, 0]);

      const xAxis = d3.axisBottom(x).tickFormat(d3.timeFormat("%H"));
      const yAxisTemperature = d3.axisLeft(yTemperature);
      const yAxisRain = d3.axisRight(yRain);
      const yAxisSunshine = d3.axisRight(ySunshine);

      svg.append("g").attr("transform", `translate(0,${height})`).call(xAxis);

      svg.append("g").call(yAxisTemperature);

      svg
        .append("g")
        .attr("transform", `translate(${width},0)`)
        .call(yAxisRain);

      svg
        .append("g")
        .attr("transform", `translate(${width + 50},0)`)
        .call(yAxisSunshine);

      const lineTemperature = d3
        .line()
        .x((d, i) => x(time[i]))
        .y((d) => yTemperature(d));

      const lineRain = d3
        .line()
        .x((d, i) => x(time[i]))
        .y((d) => yRain(d));

      const lineSunshine = d3
        .line()
        .x((d, i) => x(time[i]))
        .y((d) => ySunshine(d));

      svg
        .append("path")
        .datum(temperature)
        .attr("fill", "none")
        .attr("stroke", "red")
        .attr("stroke-width", 1.5)
        .attr("d", lineTemperature);

      svg
        .append("path")
        .datum(rain)
        .attr("fill", "none")
        .attr("stroke", "blue")
        .attr("stroke-width", 1.5)
        .attr("d", lineRain);

      svg
        .append("path")
        .datum(sunshine)
        .attr("fill", "none")
        .attr("stroke", "green")
        .attr("stroke-width", 1.5)
        .attr("d", lineSunshine);
    }
  }, [weatherData]);

  const range = (start, stop, step) =>
    Array.from({ length: (stop - start) / step }, (_, i) => start + i * step);

  return <svg ref={svgRef}></svg>;
};

export default WeatherGraph;
